.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: stretch;
      align-items: stretch;

  width: 100vw;
  min-height: 100vh;
}

.menu {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	background-color: lightgray;
	width: 100vw;
}

.dungeonDisplay {
	background-color: #ccc;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	
	display: -ms-flexbox;
	
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.dungeonDisplay > canvas {
	background-color: white;
	outline: solid 5px #666;
}


